import React, { Component } from 'react';
import style from './styles/app.scss'
import Home from './components/home'
import './semantic/dist/semantic.min.css'

class App extends Component {
  render() {
    return (
        <div className={style.component}>
            <Home />
                <h1>
                    Hi, I'm Thibault
                </h1>
        </div>
    );
  }
}

export default App;



